* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.main-header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  display: flex;
}

.logo img {
  height: 40px;
  margin-left: 50px;
}

.main-menu {
  flex-grow: 1;
}

.main-menu__list {
  flex-wrap: wrap;
  justify-content: flex-end;
  padding-right: 50px;
  list-style: none;
  display: flex;
}

.main-menu__item {
  margin: 0 20px;
  position: relative;
}

.main-menu ul li a {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 54px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}

.highlight, .is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 8px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main-menu ul li a:hover:after {
  content: " ";
  background-color: #00acdc;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}
